import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import { Train, KingBed, Public } from "@material-ui/icons";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import InfoArea from "components/InfoArea/InfoArea.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";

const useStyles = makeStyles(styles);

export default function IntroduceSection() {
    const classes = useStyles();
    return (
        <div className={classes.section}>
            <div>
                <GridContainer>
                    <GridItem xs={12} sm={12} md={4}>
                        <InfoArea
                            title="Tri Nghim Du Lch"
                            description="L mt huyn min ni nm  pha Ty ca tnh Khnh Ho. Khnh Sn c v nh  Lt th 2 ca c nc."
                            icon={Train}
                            iconColor="info"
                            vertical
                        />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={4}>
                        <InfoArea
                            title="Ni  Tin Nghi"
                            description="Khch sn sang trng v tin nghi vi nhng cnh vt tuyt vi. m bo cho du khch ni ngh ngi thoi mi."
                            icon={KingBed}
                            iconColor="success"
                            vertical
                        />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={4}>
                        <InfoArea
                            title="Travel Guide"
                            description="i ng HDV nng ng v nhiu kinh nghim s lun ng hnh cng bn trong qu trnh tham quan."
                            icon={Public}
                            iconColor="danger"
                            vertical
                        />
                    </GridItem>
                </GridContainer>
            </div>
        </div>
    );
}
